<style type='text/scss' lang="scss">
  .page-animation {
    position: absolute;
    width: 100%;
    /*transition: all .3s linear;*/
		transition: opacity .5s;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .3s ease;
  }

  .fade-enter, .fade-leave-active {
    opacity: 0
  }

  .slide-left-enter, .slide-right-leave-active {
    opacity: 0;
    transform: translate(3.75rem, 0);
  }

  .slide-left-leave-active, .slide-right-enter {
    opacity: 0;
    transform: translate(-3.75rem, 0);
  }
</style>

<template>
  <transition :name="transitionName">
    <router-view class="page-animation"></router-view>
  </transition>
</template>

<script>
let transitionName = 'fade';

export default {
  name: 'zfb-animation',
  data () {
    return {
      transitionName: transitionName
    };
  },
  beforeRouteEnter (to, from, next) {
    if (from.meta && from.meta.transitionName) {
      transitionName = from.meta.transitionName;
    } else {
      transitionName = 'fade';
    }
    next();
  },
  beforeRouteUpdate (to, from, next) {
    if (from.meta.transitionName) {
      this.transitionName = from.meta.transitionName;
    } else if (to.query.timestamp || (from.query.back)) {
      this.transitionName = 'slide-right';
    } else {
      this.transitionName = 'slide-left';
    }
    next();
  }
};
</script>



